html, body {
  font-family: Calibri, sans-serif;
  font-size: 1rem;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #dfdfdf;
}

.bio-box {
  margin: auto;
  margin-top: 20px;
  width: 30%;
  background-color: #202020;
  border-radius: 5px;
  padding: 50px 10px 55px 10px;
}

@media (max-width: 768px) {
  .bio-box {
    margin: auto;
    margin: 1% 1% 0 1%;
    box-sizing: border-box;
    width: 98%;
    background-color: #202020;
    border-radius: 5px;
    padding: 50px 10px 55px 10px;
  }
}

.bio {
  color: white;
  text-align: center;
  max-height: 100%;
  max-width: 100%;
}

#image {
  border-radius: 50%;
  max-width: 35%;
  max-height: 35%;
  content: url(./images/daniel.jpg)
}

#name {
  margin-top: 5%;
  font-size: 1.3rem;
}

#title-master {
  margin-top: 5%;
  font-size: .8rem;
}

#title-bachelor {
  font-size: .8rem;
  margin-top: 5%;
}

.links-container {
  margin: auto;
  margin-top: 10%;
  max-width: 50%;
  display: flex;
  justify-content: space-evenly;
}

#link {
  cursor: pointer;
}

.footer {
  margin-top: 20px;
  width: 100%;
  text-align: center;
  font-size: 0.7rem;
}

.academics {
  margin: 1% 1% 0 1%;
}

.masters {
  background-color: #202020;
  height: fit-content;
  margin-top: 1%;
  color: white;
}

.projects {
  background-color: #202020;
  color: white;
}

.projects-info {
  margin: 1%;
  overflow: hidden;
}

.projects .associated {
  display: flex;
  align-items: center;
}

.projects img {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.projects #date, .projects h1 {
  margin: 0;
  margin-top: 10px;
}

.masters img {
  margin: 1% 1% 1% 1%;
}

.masters-info {
  display: flex;
  align-items: flex-start;
}

.masters-info #school {
  font-size: 1rem;
}

.masters-info #course {
  font-size: .9rem;
}

.masters-info #date {
  font-size: .8rem;
}

h1 {
  font-size: 1.3rem;
}
